/* EventDetails.css */

.event-details {
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    max-width: 400px;
    margin: 0 auto;
    position: relative;
    transition: transform 0.5s ease-in-out;
  }
  
  .event-details.slide-in {
    transform: translateX(0);
  }
  
  .event-details.slide-out {
    transform: translateX(100%);
  }
  